import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController, ModalController, ToastController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { api_base_url, api_user, api_pass, sc_code, sender_id, oneSignalAppId } from '../../config';
import { SoapService } from '../soap.service';
import { CurrencyPipe } from '@angular/common';
import { OneSignal } from '@ionic-native/onesignal';

/**
 * Generated class for the IpcContactDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-ipc-contact-detail',
  providers: [SoapService],
  templateUrl: 'ipc-contact-detail.html',
})
export class IpcContactDetailPage {
  data: any;
  komentar: any = '';
  isLoading: boolean = true;
  loading: any;
  ipcContactDetail: any;
  userdataTPK: any;
  modul: any;


  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public storage: Storage,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public soapService: SoapService,
    public currencyPipe: CurrencyPipe,
    public modalCtrl: ModalController,
    public oneSignal: OneSignal,
    public toastCtrl: ToastController,
  ) {
    this.modul = navParams.get('modul');
    oneSignal.startInit(oneSignalAppId, sender_id);
    oneSignal.endInit();
    storage.get('userdataTPK').then((val) => {
      this.userdataTPK = val;

    });
    this.data = navParams.get('data');
    // console.log(this.data);
    this.getDetail();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad IpcContactDetailPage');
  }

  getDetail() {
    this.loadingPresent();
    this.isLoading = true;
    this.soapService
      //.post(api_base_url,'eoffice_bypass_wso',{fStream:JSON.stringify(
      .post(api_base_url, 'eoffice_data_detail_ipcc', {
        fStream: JSON.stringify(
          {
            // sc_type: 'restitusidtl',
            // sc_code: sc_code,        
            // data : {
            //   restitusi_id : this.restitusi['RESTITUSI_ID']
            // }  
            usernameEDI: api_user,
            passwordEDI: api_pass,
            id_pengajuan: this.data['ID_PENGAJUAN']
          }
        )
      }).then(result => {
        var responData = JSON.parse(String(result));
        // console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {
          this.ipcContactDetail = [];
          this.ipcContactDetail = responData['data'];
          // console.log(this.ipcContactDetail);
        } else {
          let toast = this.toastCtrl.create({
            message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
        }
        this.loadingDismiss();
        this.isLoading = false;
      })
      .catch(error => {
        // console.log(error);
        let toast = this.toastCtrl.create({
          message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();
        this.loadingDismiss();
        this.isLoading = false;
      });
  }

  sendAnswer() {
    // console.log(this.userdataTPK['data']['NIPP']);
    let loading = this.loadingCtrl.create({
      spinner: 'dots',
      content: "Mohon Tunggu...",
      cssClass: 'transparent',
      //dismissOnPageChange:true
    });
    loading.present();
    this.soapService
      //.post(api_base_url,'eoffice_bypass_wso',{fStream:JSON.stringify(
      .post(api_base_url, 'eoffice_pengajuan_replay_ipcc', {
        fStream: JSON.stringify(
          {
            // sc_type: 'restitusidtl',
            // sc_code: sc_code,        
            // data : {
            //   restitusi_id : this.restitusi['RESTITUSI_ID']
            // }  
            usernameEDI: api_user,
            passwordEDI: api_pass,
            replay_nipp: this.userdataTPK['data']['NIPP'],
            id_pengajuan: this.data['ID_PENGAJUAN'],
            isi: this.komentar
          }
        )
      }).then(result => {
        var responData = JSON.parse(String(result));
        // console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {
          this.getDetail();
          if (this.ipcContactDetail[0]['FROM_NIPP'] == this.userdataTPK['data']['NIPP']) {
            this.sendNotif(this.ipcContactDetail[0]['KATEGORI'], 'toadmin');
          } else if (this.ipcContactDetail[0]['FROM_NIPP'] != this.userdataTPK['data']['NIPP']) {
            this.sendNotif(this.ipcContactDetail[0]['FROM_NIPP'], 'touser');
          }
          this.komentar = '';
        } else {
          let toast = this.toastCtrl.create({
            message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
        }
        loading.dismiss();
        this.isLoading = false;
      })
      .catch(error => {
        // console.log(error);
        let toast = this.toastCtrl.create({
          message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();
      });
  }

  sendNotif(data, type) {
    var title = '';
    if (this.modul == 'hr_contact') {
      title = 'HR Contact';
    } else if (this.modul == 'helpdesk') {
      title = 'Helpdesk';
    }
    this.soapService
      .post(api_base_url, 'eoffice_notif_imove_nipp', {
        fStream: JSON.stringify(
          {
            usernameEDI: api_user,
            passwordEDI: api_pass,
            nipp: type == 'touser' ? [data] : [],
            data: {
              "res": "IpcContactDetailPage",
              "data": this.data
            },
            content: {
              "en": "Permasalahan mendapatkan balasan dari " + this.userdataTPK['data']['NAMA']
            },
            heading: {
              "en": title
            },
            id_kategori: type == 'toadmin' ? data : ""
          }

        )
      }).then(result => {
        let hasil = JSON.parse(String(result));
        // console.log("push notif : " + hasil);
        //this.navCtrl.pop();
      }).catch(error => {

        // console.log(error);
        this.navCtrl.pop();

      });
  }

  openModalRating(data) {

    let modal = this.modalCtrl.create("IpcContactRatingPage", {
      // kepadaList : this.userdata['data']['LISTOFFICER'],
      "data": data
    }, {
      enableBackdropDismiss: true,
      showBackdrop: true,
      cssClass: 'my-modal2'
    });

    if (this.ipcContactDetail[0]['FROM_NIPP'] != this.userdataTPK['data']['NIPP']) {
      let loading = this.loadingCtrl.create({
        spinner: 'dots',
        content: "mohon tunggu...",       
        cssClass: 'transparent',
        //dismissOnPageChange:true
      });
      loading.present();    
      this.soapService    
      .post(api_base_url,'eoffice_rating_ipcc',{fStream:JSON.stringify(
        {
          usernameEDI : api_user, 
          passwordEDI : api_pass  ,      
          rate: 0,
          id_pengajuan: this.data['ID_PENGAJUAN']        
        }
      )}).then(result => {
        var responData = JSON.parse(String(result));
        // console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {
          let toast = this.toastCtrl.create({
            message: 'Close Case Berhasil !',
            duration: 3000,
            position: 'bottom'
          });
          toast.present().then(() => {
            this.navCtrl.pop();
          });
  
        } else {        
          let toast = this.toastCtrl.create({
            message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present(); 
        }  
        loading.dismiss();       
      })
      .catch(error => {
        // console.log(error);
        let toast = this.toastCtrl.create({
          message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();
  
        loading.dismiss();            
        
      });
    } else {
      modal.present();
      modal.onDidDismiss(data => {
        // console.log(data);
        if (data != null) {
          this.navCtrl.pop();
          this.soapService
            .post(api_base_url, 'eoffice_notif_imove_nipp', {
              fStream: JSON.stringify(
                {
                  usernameEDI: api_user,
                  passwordEDI: api_pass,
                  nipp: [],
                  data: {
                    "res": "IpcContactDetailPage",
                    "data": this.data
                  },
                  content: {
                    "en": "Permasalahan Telah Ditutup Oleh " + this.ipcContactDetail[0]['FROM_NAMA']
                  },
                  heading: {
                    "en": "Rating"
                  },
                  id_kategori: this.ipcContactDetail[0]['KATEGORI']
                }

              )
            }).then(result => {
              let hasil = JSON.parse(String(result));
              // console.log("push notif : " + hasil);
              //this.navCtrl.pop();
            }).catch(error => {
              // console.log(error);
              this.navCtrl.pop();
            });
        }
      });
    }
  }

  loadingPresent() {
    this.loading = this.loadingCtrl.create({
      spinner: 'dots',
      content: "Mohon Tunggu...",
      cssClass: 'transparent',
      //dismissOnPageChange:true
    });
    this.loading.present();
  }

  loadingDismiss() {
    if (this.loading.present()) {
      this.loading.dismiss();
    }
  }


}



